import "./styles.css";
import React, { useState } from "react";
import {
  LineChart,
  Line,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
  ResponsiveContainer,
} from "recharts";
import dayjs from "dayjs";

const parseCSV = (csv: string) => {
  const lines = csv.trim().split("\n");
  const headers = lines[0].split(";");
  const data = lines.slice(1).map((line) => {
    const values = line.split(";");
    const entry: Record<string, string | number | Date> = {};
    headers.forEach((header, index) => {
      if (header === "datetime") {
        entry[header] = new Date(values[index]); // Преобразуем в дату
      } else {
        entry[header] = parseFloat(values[index]); // Преобразуем в число
      }
    });
    return entry;
  });
  return data;
};

function datetimeFormatter(tick: string): string {
    return dayjs(tick).format("DD.MM.YYYY HH:mm");
}

export default function App() {
  const [data, setData] = useState<any[]>([]);

  const handleFileUpload = (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (!file) return;

    const reader = new FileReader();
    reader.onload = (e) => {
      const text = e.target?.result as string;
      const parsedData = parseCSV(text);
      setData(parsedData);
    };
    reader.readAsText(file);
  };

  return (
    <div>
      <h1>Home Climate Viewer</h1>
      <input type="file" accept=".csv" onChange={handleFileUpload} />
      {data.length > 0 ? (
        <ResponsiveContainer width={"100%"} height={300}>
          <LineChart data={data} margin={{ top: 20 }}>
            <CartesianGrid strokeDasharray="3 3" />
            <XAxis
              dataKey="datetime"
              tickFormatter={datetimeFormatter}
            />
            <YAxis />
            <Tooltip
              labelFormatter={(value) =>
                `Time: ${datetimeFormatter(value)}`
              }
            />
            <Legend />
            <Line
              type="monotone"
              dataKey="temperature"
              stroke="#8884d8"
              activeDot={{ r: 8 }}
              name="Temperature (°C)"
            />
            <Line
              type="monotone"
              dataKey="humidity"
              stroke="#82ca9d"
              name="Humidity (%)"
            />
          </LineChart>
        </ResponsiveContainer>
      ) : (
        <p>Please upload a CSV file to visualize the data.</p>
      )}
    </div>
  );
}